import {addLocaleData} from 'react-intl';
import EnLang from './entries/en-US';
import EsLang from './entries/es-ES';
import DeLang from './entries/de-DE';
import CaLang from './entries/ca-ES';

const AppLocale = {
    en: EnLang,
    es: EsLang,
    ca: CaLang,
    de: DeLang,
};
Object.keys(AppLocale).forEach(function(key) {
    // console.log(`Adding locale ${key}`);
    addLocaleData(AppLocale[key]);
})
// addLocaleData(AppLocale.en);

export default AppLocale;
